.about-section {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-title-container {
  display: flex;
  align-items: center;
  width: 50%;
  height: 100px;
}

.about-heading {
  font-size: 4rem;
}

.about-divider {
  width: 100%;
  height: 1px;
  margin-left: 25px;
  background: linear-gradient(145deg, #c1b, #72f);
}

.about-content {
  font-size: 20px;
  line-height: 1.5;
}

.about-pic-container {
  display: flex;
  justify-content: center;
}

.about-pic {
  height: 200px;
  border-radius: 50%;
  background: linear-gradient(145deg, #c1b, #72f);
  padding: 4px;
}

@media screen and (max-width: 768px) {
  .about-title-container {
    width: 100%;
  }

  .about-heading {
    font-size: 40px;
  }

  .about-content {
    font-size: 18px;
  }
}
