.skill-section {
  min-height: 100vh;
}

.skill-title-container {
  display: flex;
  align-items: center;
  width: 50%;
  height: 100px;
}

.skill-divider {
  width: 100%;
  height: 1px;
  margin-left: 25px;
  background: linear-gradient(145deg, #c1b, #72f);
}

.skill-heading {
  text-align: center;
  font-size: 4rem;
}

.skill-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 150px;
  font-size: 1.5rem;
  font-weight: bold;
}

@media screen and (max-width: 768px) {
  .skill-title-container {
    width: 100%;
  }

  .skill-heading {
    font-size: 40px;
  }
}
