.contact-section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contact-heading {
  font-size: 3.5rem;
  margin: 0;
}

.contact-subtext {
  font-size: 1.25rem;
  color: #8f9094;
  text-align: center;
}

.contact-btn-group {
  display: flex;
  gap: 0.5rem;
}

.contact-btn {
  padding: 0.5rem 1rem;
  background-color: hsla(0, 0%, 100%, 0.1);
  color: white;
  border: 0;
  border-radius: 0.25rem;
  cursor: pointer;
  text-decoration: none;
}

