.project-card {
  width: 100%;
  height: 300px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  background-color: #1a1a1a;
  border-radius: 0.5rem;
}

.project-card-top {
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project-card-top-right {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.project-card-top-right > * {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

.project-card-middle {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.5rem;
}

.project-name {
  font-size: 1.5rem;
  margin: 0;
}

.project-description {
  font-size: 0.9rem;
  margin: 0;
}

.project-card-bottom {
  height: 50px;
}

.project-card-bottom > ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.project-tech {
  font-size: 12px;
  color: #8f9094;
}
