.offcanvas {
  position: fixed;
  bottom: 0;
  z-index: 1045;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  background-color: #141414;
  background-clip: padding-box;
  outline: 0;
  transition: transform 0.3s ease-in-out;
}

.offcanvas-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  opacity: 0.5;
}

.offcanvas-end {
  top: 0;
  right: 0;
  width: 65vw;
  max-width: 400px;
  border-left: 1px solid rgba(0, 0, 0, 0.2);
  transform: translateX(100%);
}

.show {
  transform: none;
  box-shadow: 15px 15px 15px 15px black;
}

.offcanvas-btn {
  position: absolute;
  top: 2rem;
  right: 2rem;
  background-color: hsla(0, 0%, 100%, 0.1);
  border: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  border-radius: 50%;
  cursor: pointer;
}

.offcanvas-list {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 4rem;
}

.offcanvas-item {
  padding: 0;
}
