.intro-section {
  height: calc(100vh - 100px);
  display: flex;
  align-items: center;
}

.intro-content {
  width: 75%;
}

.intro-heading {
  font-size: clamp(40px, 8vw, 80px);
  font-weight: bold;
  line-height: 1.1;
  margin: 0;
}

.intro-subtext {
  font-size: 20px;
  color: #8f9094;
}

.intro-btn {
  cursor: pointer;
  background-color: #a2e;
  color: white;
  border: 0;
  padding: 0.75rem;
  border-radius: 0.25rem;
}

@media screen and (max-width: 992px) {
  .intro-content {
    width: 100%;
    margin-bottom: 20vh;
  }

  .intro-subtext {
    font-size: 18px;
  }
}
