.header {
  position: sticky;
  top: 0;
  background-color: #141414 !important;
  box-shadow: 0px 20px 10px -15px #111;
  z-index: 10;
}

.nav {
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-brand {
  cursor: pointer;
  font-weight: bold;
  letter-spacing: 1.1px;
  text-decoration: none;
  color: inherit;
}

.nav-links {
  display: flex;
  align-items: center;
  padding: 0;
  gap: 2rem;
}

.nav-items {
  list-style-type: none;
  cursor: pointer;
}

.nav-items:hover {
  font-weight: bold;
}

.nav-toggle {
  display: none;
  background-color: hsla(0, 0%, 100%, 0.1);
  border: 0;
  align-items: center;
  justify-content: center;
  padding: 8px;
  border-radius: 50%;
  cursor: pointer;
}

@media screen and (max-width: 992px) {
  .nav-items {
    display: none;
  }
  .nav-toggle {
    display: flex;
  }
}
